import React, { Fragment } from "react";
import PropTypes from "prop-types";
import Card from '../../components/card';
import Header from "../components/headers";
import Footer from "../components/footer";
import Empty from "../error/empty"

const BlogPresentation = ({
  navbrand,
  imageUri,
  navlinks,
  headerBody,
  headerTitle,
  FooterBrandBody,
  posts,
  onCardClick
}) => {
  return (
    <Fragment>
      <Header
        brand={navbrand}
        image={imageUri}
        navItems={navlinks}
        title={headerTitle}
        body={headerBody}
        height={"40vh"}
        center={true}
      />
      <div className="container pt-4 mb-5">
        <div className="row course-list mt-5 mb-5">
          {posts.length ? (
            posts.map((data) => (
              <Card
                className="mt-4 mt-sm-0"
                click={() => onCardClick(data._id)}
                id={data._id}
                title={data.title}
                body={data.description}
                img={data.img}
              >
                <p className="blog-meta font14 mt20">
                  {`published on ${data.publishAt}`}
                </p>
              </Card>
            ))
          ) : (
            <Empty height="60%" />
          )}
        </div>
      </div>
      <Footer navItem={navlinks} brandBody={FooterBrandBody} />
    </Fragment>
  );
};

BlogPresentation.defaultProps = {
  posts: []
};
BlogPresentation.propTypes = {
  navbrand: PropTypes.string,
  imageUri: PropTypes.string,
  navlinks: PropTypes.array,
  headerBody: PropTypes.string,
  headerTitle: PropTypes.string,
  FooterBrandBody: PropTypes.string,
  posts: PropTypes.array
};

export default BlogPresentation;
