import React, { Component } from "react";
import { connect } from "react-redux";
import {
  getBlogsState,
  getNavBrandState,
  getNavLinkState,
} from "../../store/reselect";
import slide2 from "../../assets/nurse.jpg";
import { withRouter } from 'react-router-dom';
import View from "../../presentations/blog";
import { FooterBrandBody } from "../../utils/data";
import { getPostsAction } from "../../store/action";

class Blog extends Component {
  state = {
    title: "News",
    src: slide2,
  };

  componentDidMount() {
    this.props.getPosts();
  }

  handleCardClick = (id) => {
    this.props.history.push('/news-detail', { id })
  }

  render() {
    let {
      state: { title, src },
      props: { brand, navlinks, posts },
      handleCardClick
    } = this;
    return (
      <View
        navbrand={brand}
        imageUri={src}
        navlinks={navlinks}
        headerTitle={title}
        onCardClick={handleCardClick}
        FooterBrandBody={FooterBrandBody}
        posts={posts}
      />
    );
  }
}

const mapStateToProps = (state) => ({
  brand: getNavBrandState(state),
  navlinks: getNavLinkState(state),
  posts: getBlogsState(state),
});

const mapDispatchToProps = (dispatch) => {
  return {
    getPosts: () => dispatch(getPostsAction()),
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Blog));

